const string = `* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

*::before, *::after {
    box-sizing: border-box;
}

.doraemon {
    position: relative;
    min-height: 50vh;
    width: 100%;
    background: rgb(0, 160, 232);
}

.doraemon .bighead {
    width: 420px;
    height: 400px;
    position: absolute;
    background-color: rgb(0, 160, 232);
    left: 50%;
    top: 10px;
    margin-left: -210px;
    border-radius: 50% 50% 45% 45%;
}

.doraemon .face1 {
    border: 3px solid #000;
    width: 360px;
    height: 300px;
    position: absolute;
    left: 50%;
    top: 110px;
    margin-left: -180px;
    background-color: #FFF;
    border-radius: 50% 50% 45% 45%;
}

.doraemon .nose1 {
    border: 3px solid black;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #FF0000;
    top: 160px;
    left: 50%;
    position: relative;
    margin-left: -25px;
    z-index: 3
}

.doraemon .nose1.shadow {
    border: none;
    position: absolute;
    left: 48px;
    width: 15px;
    height: 15px;
    background-color: #FFF;
    border-radius: 50%;
}

.doraemon .nosedown {
    height: 130px;
    width: 3px;
    position: absolute;
    top: 210px;
    left: 50%;
    border-left: 3px solid #000;
    z-index: 3
}

.doraemon .eye1 {
    border: 3px solid black;
    width: 90px;
    height: 110px;
    border-radius: 45% 45% 45% 45%;
    background-color: #FFF;
    position: absolute;
    top: 60px;
    left: 50%;
    z-index: 3;
}

.doraemon .eye1.left {
    transform: translateX(-90px);
}

.doraemon .eyeball {
    position: absolute;
    top: 60px;
    width: 20px;
    height: 20px;
    background-color: #000;
    border-radius: 50%;
    margin-left: 10px;
    -webkit-animation: cate1 2s infinite linear;
}

@-webkit-keyframes cate1 {
    0% {
        margin-left: 10px;
        top: 60px;
    }
    25% {
        margin-left: 18px;
        top: 64px;
    }
    50% {
        margin-left: 10px;
        top: 68px;
    }
    75% {
        margin-left: 2px;
        top: 64px;
    }
    100% {
        margin-left: 10px;
        top: 60px;
    }
}

.doraemon .eyeball.left {
    transform: translateX(45px);
}

.doraemon .mouth1 {
    position: relative;
    height: 180px;
    width: 240px;
    top: 110px;
    left: 50%;
    margin-left: -120px;
    background: #fff;
    border-bottom: 3px solid #000;
    border-radius: 0 0 40% 40%;
    z-index: 2;

}

.doraemon .tougue1 {
    position: absolute;
    height: 96px;
    width: 80px;
    top: 270px;
    left: 50%;
    margin-left: 20px;
    border: 2px solid #000;
    border-radius: 0 0 40% 40%;
    transform: rotate(-20deg);
    background: #ff485f;
    z-index: 1;
    -webkit-animation: cate2 3s infinite linear;
}

@-webkit-keyframes cate2 {
    0% {
        margin-left: 20px;
        transform: rotate(-20deg);
    }
    25% {
        margin-left: -40px;
        transform: rotate(0deg);
    }
    50% {
        margin-left: -100px;
        transform: rotate(20deg);
    }
    75% {
        margin-left: -40px;
        transform: rotate(0deg);
    }
    100% {
        margin-left: 20px;
        transform: rotate(-20deg);
    }
}

.doraemon .tougueline {
    position: absolute;
    height: 48px;
    width: 1px;
    top: 30px;
    left: 10px;
    border: 1px solid #000;
    margin-left: 30px;
    z-index: 3;
}

.doraemon .whiskers {
    border-top: 3px solid #000;
    position: absolute;
    height: 60px;
    width: 100px;
    left: 50%;
    z-index: 3;
}

.doraemon .wl1 {
    top: 210px;
    margin-left: -150px;
    transform: rotate(20deg);
}

.doraemon .wl2 {
    top: 240px;
    margin-left: -150px;
}

.doraemon .wl3 {
    top: 270px;
    margin-left: -130px;
    transform: rotate(-20deg);
}

.doraemon .wr1 {
    top: 210px;
    margin-left: 50px;
    transform: rotate(-20deg);
}

.doraemon .wr2 {
    top: 240px;
    margin-left: 50px;
}

.doraemon .wr3 {
    top: 270px;
    margin-left: 30px;
    transform: rotate(20deg);
}

.doraemon .necklace {
    position: absolute;
    width: 320px;
    height: 40px;
    top: 370px;
    left: 50%;
    margin-left: -160px;
    background-color: #FF0000;
    border: 3px solid #000;
    border-radius: 30px 30px 30px 30px;
    z-index: 3;
}

.doraemon .smallbell {
    position: absolute;
    width: 60px;
    height: 60px;
    top: 380px;
    left: 50%;
    margin-left: -30px;
    background-color: #ffe600;
    border: 3px solid #000;
    border-radius: 50%;
    z-index: 3;
}

.doraemon .bell1 {
    position: absolute;
    width: 60px;
    height: 6px;
    top: 10px;
    left: 50%;
    margin-left: -30px;
    background-color: #ffe600;
    border: 2px solid #000;
    border-radius: 16px;
}

.doraemon .bell2 {
    position: absolute;
    top: 30px;
    left: 50%;
    width: 20px;
    height: 10px;
    margin-left: -10px;
    background-color: #000;
    border-radius: 50%;
}

.doraemon .bell3 {
    position: absolute;
    width: 30px;
    height: 20px;
    top: 37px;
    left: 50%;
    margin-left: -3px;
    border-left: 6px solid #000;
}


.pikachu {
    background: #ffe600;
    min-height: 50vh;
    position: relative;

}

.pikachu .eye {
    border: 3px solid black;
    width: 64px;
    height: 64px;
    position: absolute;
    left: 50%;
    top: 100px;
    margin-left: -32px;
    background: #2e2e2e;
    border-radius: 50%;
}

.pikachu .eye::before {
    content: '';
    display: block;
    border: 3px solid black;
    width: 30px;
    height: 30px;
    background: #fff;
    border-radius: 50%;
    position: relative;
    left: 4px;
    top: 0;
}

.pikachu .eye.left {
    transform: translateX(-100px);
}

.pikachu .eye.right {
    transform: translateX(100px);
}

.pikachu .nose {
    border-bottom: none;
    width: 0;
    height: 0;
    position: relative;
    left: 50%;
    top: 140px;
    margin-left: -8px;
    z-index: 3;
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }
    33% {
        transform: rotate(8deg);
    }
    66% {
        transform: rotate(-8deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

.pikachu .nose {
    transform-origin: center bottom;
    animation: wave 500ms infinite linear;
}

.pikachu .noseTop {
    position: absolute;
    width: 16px;
    height: 6px;
    top: -14px;
    left: -8px;
    border-radius: 10px 10px 0 0;
    background: #2e2e2e;
}

.pikachu .mouth {
    width: 140px;
    height: 140px;
    position: absolute;
    left: 50%;
    top: 180px;
    margin-left: -70px;
}

.pikachu .mouth .top {
    position: relative;
    top: -36px;
    z-index: 2;

}

.pikachu .mouth .top .lip {
    border: 3px solid black;
    height: 20px;
    width: 70px;
    border-top-color: transparent;
    border-right-color: transparent;
    position: relative;
    position: absolute;
    left: 50%;
    margin-left: -35px;
    background: #ffe600;
    z-index: 2;
}

.pikachu .mouth .top .lip.left {
    border-radius: 0 0 0 20px;
    transform: rotate(-20deg) translateX(-35px);
}

.pikachu .mouth .top .lip.right {
    border-radius: 0 0 20px 0;
    transform: rotate(20deg) translateX(35px);;
}

.pikachu .mouth .top .lip::before {
    border: 12px solid #ffe600;
    content: '';
    display: block;
    width: 90px;
    height: 30px;
    position: absolute;
    border-bottom-color: transparent;
    bottom: -3px;
    background: transparent;
}

.pikachu .mouth .top .lip.left::before {
    border-left-color: transparent;
    right: -4px;
}

.pikachu .mouth .top .lip.right::before {
    border-right-color: transparent;
    left: -4px;
}

.pikachu .mouth .bottom {
    height: 140px;
    width: 100%;
    position: absolute;
    top: -20px;
    overflow: hidden;
}

.pikachu .mouth .bottom .tongue1 {
    border: 3px solid black;
    width: 120px;
    height: 600px;
    position: absolute;
    bottom: 0px;
    left: 50%;
    margin-left: -60px;
    border-radius: 60px/300px;
    background: #9b000a;
    overflow: hidden;
}

.pikachu .mouth .bottom .tongue1 .tongue2 {
    width: 120px;
    height: 140px;
    position: absolute;
    bottom: -30px;
    left: 50%;
    margin-left: -60px;
    border-radius: 50%;
    background: #ff485f;
    overflow: hidden;
    z-index: 0;
}

.pikachu .face {
    border: 3px solid black;
    width: 80px;
    height: 80px;
    position: absolute;
    left: 50%;
    border-radius: 50%;
    background: red;
    margin-left: -40px;
    top: 200px;
}

.pikachu .face > img {
    position: absolute;
    top: 50%;
    left: 50%;
}

.pikachu .face.left {
    transform: translateX(-150px);
}

.pikachu .face.left > img {
    transform: rotateY(180deg);
    transform-origin: 0 0;
}

.pikachu .face.right {
    transform: translateX(150px);
}

`

export default string